





























































export default {
	name: "components",
	data() {
		return {
			fkmoney:'0.00',
			czmoney:'0.00',
			jlbmoney:'0.00',
			
			StatusBar: this.StatusBar,
			CustomBar: this.CustomBar,
			src:'',
			
			modalName: null,
			gridCol: 3,
			gridBorder: true,
			
			menuBorder: true,
			menuArrow: true,
			menuCard: true,
			
			cardCur: 0,
			dotStyle: true,
			towerStart: 0,
			direction: '',
			token:'',
		};
	},
	//加载金额
	created:function(){
		const agentInfo = uni.getStorageSync('agentInfo');
		if (agentInfo) {
			this.token=agentInfo.token;
		}
		uni.request({
			url: 'http://192.168.0.199:8080/agent/earnings/ajax-earn',
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				method: 'POST',
				dataType: 'json',
				cache: false,
				data: {
					token:this.token,
				},
				success: res => {
					let data=res.data;
					if(data.code==200){
						this.fkmoney=data.data.fkmoney;
						this.zcmoney=data.data.zcmoney;
						this.jlbmoney=data.data.jlbmoney;
					}
				},
				fail: () => {},
				complete: () => {}
			});

	},
	methods: {
		// cardSwiper
		cardSwiper(e) {
			this.cardCur = e.detail.current
		},	
		//页面跳转
		PageChange: function(e) {
			if(e.currentTarget.dataset.cur==""){
				uni.showToast({
					icon: 'none',
					title: '暂未开放'
				},1000);
			}else{
				uni.redirectTo({
					url:e.currentTarget.dataset.cur
				});
			}
		},
	}
}
